.calc-container {
  padding-top: 110px;
  background-color: rgb(64, 67, 114);
  min-width: 100vh;
  height: 1000vh;
  display: flex;
  justify-content: center;
  #calc {
    width: 300px;
    height: 500px;
    @media (max-width: 700px) {
      width: 80%;
    }
    @media (max-width: 400px) {
      width: 99%;
    }
    background-color: rgb(0, 0, 0);
    border-color: black;
    border-style: solid;
    border-width: 5px;
    border-radius: 8px;
    box-shadow: 0 0 20px rgb(29, 8, 48);
    display: grid;
    gap: 3px;
    justify-items: center;
    align-items: center;
    grid-template-areas:
      "display display display display"
      "clear clear divide multiply"
      "seven eight nine subtract"
      "four five six add"
      "one two three equals"
      "zero zero decimal equals";
    > div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      border: 1px solid black;
      border-radius: 8px;
      background-color: rgb(238, 227, 130);
      font-weight: bold;
      font-size: 1.9em;
    }
    .screen {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      border: 1px solid rgb(0, 0, 0);
      background-color: rgb(238, 237, 227);
      height: fit-content;
    }
    .screen-line {
      text-align: right;
      width: 100%;
      padding-right: 10px;
    }
    #history {
      background-color: rgb(212, 212, 212);
      color: rgb(117, 117, 117);
      min-height: 28px;
      height: fit-content;
      font-size: 0.6em;
    }
    #display {
      background-color: rgb(255, 255, 255);
    }
    #clear {
      background-color: rgb(209, 88, 51);
    }
    #divide,
    #add,
    #multiply,
    #subtract,
    #equals {
      background-color: rgb(228, 205, 4);
    }
  }
}
